import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Portfolio from '@/components/portfolio/Portfolio.vue'
import Stocks from '@/components/stocks/Stocks.vue'

Vue.use(Router)

export default new Router({
mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/portfolio',
      name: 'Portfolio',
      component: Portfolio
    },
    {
      path: '/stocks',
      name: 'Stocks',
      component: Stocks
    },
    {
      path: '*',
      redirect: '/'
    }
  ]
})



