<template>

<div>
    <app-stock v-for="stock in getstocks" :stock="stock"></app-stock>
</div>

</template>

<script>

import Stock from './Stock.vue';

export default {
    components: {
        appStock: Stock
    },
    computed: {
        getstocks() {
            return this.$store.getters.stocks;
            //return this.$store.getters.getstocks;
        }
    }
}

</script>



